// App.js
import React, { useState, useEffect } from "react";
import Header from "./components/Header/Header"
import LoginForm from "./components/LoginForm/LoginForm"
import JoinStartForm from "./components/JoinStartForm/JoinStartForm"
import "./App.css";
import { connect, sendMsg } from "./api";
import { Button,Box,Grommet } from 'grommet';

var token; 

function send(msg) {
  console.log(msg);
  sendMsg(msg);
}

function App(props) {
  const [message, setMessage] = useState({ "data": "" });
  const [name, setName] = useState("");
  const [token, setToken] = useState( "");

  const update = function(data){
    connect(token,(d)=>console.dir(d));
    setToken(data)
  };

  let msgText = message.data;

  const theme = {
    global: {
      font: {
        family: 'Roboto',
        size: '14px',
        height: '20px',
      },
    },
  };

  console.log("token","'"+token+"'");
  console.log("token",token=="");
  console.log("token",token!="");

  let hasToken = token != "";
console.log('hasToken',hasToken);
  return (
    <Grommet theme>
      <div className="App">
        <Box margin={{ top: 'smallish' }} elevation='depth5'>
          Celebrity Heads
        </Box>
        <Header />

        {hasToken?null:<LoginForm onLogin={update} /> }
        {hasToken?<JoinStartForm />:null}
      
        <Button onClick={() => { send("hello") }}>Hit</Button>
      </div>
    </Grommet>
  );
}

export default App;



